/* Animation */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out 0.3s;
}

.fade-leave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 1;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

@keyframes celebrate {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(30deg);
  }
  100% {
    transform: rotate(0);
  }
}

.celebrate {
  display: inline-block;
  animation-name: celebrate;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 5;
}

.chapterCatalog .question {
  text-align: center;
  padding-top: 1.5rem;
}

.cheatsheet  {
  padding: 0;
}

.cheatsheet h5 {
  border-bottom: 1px solid #ccc;
  padding-bottom: 20px;
  color: #284326;
}

.cheatsheet h5, .cheatsheet h2 {
  padding-left: 20px;
  padding-right: 20px;
}

.back-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.back-button:hover {
  background-color: #3e8e41;
}

.cheatsheet-container .button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
}
